import React from 'react';
import gemeinde from '../../../assets/umgebung/gemeinde.jpg'
import styled from 'styled-components';

const MapContainer = styled.div`
  width: 100%;
  padding: 0 0 20px;
  img {
    width: 100%;
    border: none;
  }
`;
const Container = styled.div`
  width: calc(100% - 40px);
  padding: 0 20px;
`;

const Image = styled.div`
  height: 500px;
  background-image: url(${gemeinde});
  background-position: center center;
  background-repeat: no-repeat;
  @media (max-width: 1009px) {
    height: 300px;
    background-size: 200%;
    background-position: center 40%;
  }
  @media (max-width: 679px) {
    background-size: 210%;
    background-position: center 22%;
    height: 200px;
  }
  img {
    display: none;
  }
  @media print {
    height: auto;
    img {
      width: 100%;
      display: block;
    } 
  }
`;

const Link = styled.a`
  text-decoration: none;
  color: #464;
`;

const Printp = styled.p`
  @media print {
    display: none;
  }
`;

const Gemeinde = () => {
  return (<Container>
    <p>8910 Affoltern am Albis liegt im Bezirk Knonaueramt, im Kanton Zrich. </p>

    <p>In Affoltern am Albis neben traditionsverbundenen Unternehmen neue, moderne Firmen und Geschfte Platz gefunden, die das Regionalzentrum weitherum bekannt machen. Affoltern am Albis ist Sitz verschiedener ffentlicher Einrichtungen und Dienste, die sich gegenseitig ergnzen und ein Netz sozialer Sicherheit bieten. Fr die Freizeitgestaltung stehen ein Freibad und Sportanlagen sowie ber 100 Vereine zur Verfgung.</p>

    <p>Die Gemeinde Affoltern am Albis wurde per 1. Juli 2018 zur Stadt Affoltern am Albis. Auch wenn man in Affoltern am Albis nicht mehr die einstige lndliche Idylle vorfindet, bietet der Ort seinen ber 12'000 Einwohnern mit seiner sonnigen, zentralen Lage zwischen den Metropolen Zrich, Zug und Luzern, den guten Verkehrsverbindungen und den grsstenteils gut in die Landschaft eingefgten berbauungen, dem vielen Grn und der unberbaubaren Umgebung eine vorzgliche Wohnqualitt und eine ansprechende Umwelt.</p>

    <Printp>Mehrere Fakten ber Affoltern a.A: <Link href="https://www.raiffeisen.ch/rch/de/privatkunden/hypotheken/gemeindeinfo.affoltern-am-albis.html" target="_blank">hier</Link></Printp>

    
    <MapContainer>
    <Image><img alt="Affoltern am Albis" src={gemeinde} /></Image>
      </MapContainer>
    </Container>
  );
};

export default Gemeinde;

